import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Header from './Header';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import EmailIcon from '@mui/icons-material/Email';
import { Link } from 'react-router-dom';

function About() {
  return (
    <div>
      <Row>
        <Col sm={2}>
          <Header />
        </Col>
        <Col sm={5} className='bb'>
        <h1>CONTACT US</h1>
        <div className='bb4'>
        <p>Get in Touch</p>
          <h1>Don't Be a Stranger, Just Say Hello.</h1>
          <p>We will work closely with you as an extension of your team to provide solutions.</p>
          <div className='bb1'>
            <img src={'https://html.laralink.com/prohealth/assets/img/home_1/avatar_1.png'} alt='Thomas Willimes' />
            <p>Thomas Willimes</p>
              <div className='bb2'>
               <p>Director and CEO</p>
               </div>
              
          </div>
          </div>
        </Col>
        <Col sm={5} className='bb'>
        <div className='bb3'>
        <h1>Contact us</h1>
        <input type="text" className='tes' placeholder="your name"/>
        <p></p>
        <input type="text" className='tes1' placeholder="email address"/>
        <p></p>
        <input type="number"  className='tes2'placeholder="phone no"/>
        <p></p>
        <input type="text" className='tes3' placeholder="your meassage"/>
        <p></p>
        <Link to='/Home'><button className='tes4'>send message</button></Link>
        <div className='ca'>
                <FacebookIcon/>
                <TwitterIcon/>
                <EmailIcon/>
               </div>
        </div>
        </Col>
        </Row>
       
      
     
  
    </div>
  );
}

export default About;