import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import {Link } from 'react-router-dom'
import Header from './Header';

function About() {
  return (
    <div>
      <Row>
        <Col sm={2}>
          <Header />
        </Col>
        <Col sm={10}>
          <h1>Psychology and Lifestyle</h1>

          <div className='article1'>
            <div className='article'>
              <img src='https://html.laralink.com/prohealth/assets/img/blog/post_1.jpeg' alt='' />
              <p>The Importance of Mental Health: Understanding and Managing Anxiety Disorder</p>
              <Link to='/Blog'><Button variant="primary">Learn more</Button></Link>
            </div>

            <div className='article'>
              <img src='https://html.laralink.com/prohealth/assets/img/blog/post_4.jpeg' alt=''  />
              <p>Managing Chronic Pain: Treatment Options and Strategies characterized by chronic anxiety</p>
              <Link to='/Blog'><Button variant="primary">Learn more</Button></Link>
            </div>

            <div className='article'>
              <img 
                src='https://html.laralink.com/prohealth/assets/img/blog/post_6.jpeg' 
                alt='The Role of Physical Therapy' 
              />
              <p>The Role of Physical Therapy in Injury Recovery and Prevention Heart-Healthy</p>
              <Link to='/Blog'><Button variant="primary">Learn more</Button></Link>
            </div>

            <div className='article'>
              <img 
                src='https://html.laralink.com/prohealth/assets/img/blog/post_5.jpeg' 
                alt='Understanding and Managing Diabetes' 
              />
              <p>Understanding and Managing Diabetes: Tips for Healthy Living characterized by chronic anxiety</p>
              <Link to='/Blog'><Button variant="primary">Learn more</Button></Link>
            </div>

            <div className='article'>
              <img 
                src='https://html.laralink.com/prohealth/assets/img/blog/post_3.jpeg' 
                alt='Preventing Heart Disease' 
              />
              <p>Preventing Heart Disease: Tips for a Heart-Healthy Lifestyle Options and Strategies</p>
              <Link to='/Blog'><Button variant="primary">Learn more</Button></Link>
            </div>
            <div className='article'>
              <img src='https://html.laralink.com/prohealth/assets/img/blog/post_4.jpeg' alt=''  />
              <p>Managing Chronic Pain: Treatment Options and Strategies characterized by chronic anxiety</p>
              <Link to='/Blog'><Button variant="primary">Learn more</Button></Link>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
}

export default About;