import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
import {Link } from 'react-router-dom'
import HomeIcon from '@mui/icons-material/Home';
import MedicationIcon from '@mui/icons-material/Medication';
import MedicalServicesIcon from '@mui/icons-material/MedicalServices';
import BookIcon from '@mui/icons-material/Book';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';

function Header() {
  return (
    <div className='text'>
      <ul>
        <Link to='/Home' className='cl'><li><HomeIcon/>Home</li></Link>
        <Link to='/Doctors' className='cl'><li><MedicationIcon/>Doctors</li></Link>
        <Link to='/Services' className='cl'><li><MedicalServicesIcon/>Services</li></Link>
        <Link to='/Blog' className='cl'><li><BookIcon/>Blog</li></Link>
        <Link to='/Contact'className='cl' ><li><AddIcCallIcon />Contact</li></Link>
        <Link to='/Logout' className='cl'><li>Logout</li></Link>
      </ul>

    </div>
  )
}

export default Header